import React, { Component } from 'react';

class Info extends Component {
    render() {
        return (
            <div>
                <h1>Información</h1>

                <div className="row">
                    <div className="small-8 columns text-justify">
                        <p>
                            <strong>Electrónica Marroig</strong> es una empresa fundada en 1995, cuenta con una amplia experiencia en el sector de la náutica deportiva.
                        </p>

                        <p>Nuestra actividad principal está centrada en la venta, instalación y mantenimiento de equipos electrónicos para la navegación, gps plottes, radares, pilotos y comunicaciones náuticas. Desarrollamos nuestra actividad en todo el litoral mallorquín.</p>
                    </div>

                    <div className="small-4 columns">
                        <img className="bordered-img" src="resources/img/furgo.png" />
                    </div>
                </div>

                <div className="row">
                    <div className="small-12 columns">
                        <h3 className="subheader">Electrónica</h3>
                        <p>Instalación de sistemas de navegación, gps plotter, radar, pilotos automáticos, sondas, instrumentación, AIS ...</p>

                        <h3 className="subheader">Comunicaciones</h3>
                        <p>Instalación de sistemas de comunicación, radioteléfonos, VHF, BLU ...</p>

                        <h3 className="subheader">Seguridad y salvamento</h3>
                        <p>Radiobalizas, Navtex, transponders ...</p>

                        <h3 className="subheader">Servicio barco pesca profesional</h3>
                        <p>Cajas azules, AIS, teléfonos SAT ...</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default Info;



